*
{
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

/* Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Unbounded&display=swap');


:root 
{
  --background-color: lime;
  --modal-text-color: rgb(255, 255, 255);
  --primary-blue: #1619d1;
  --secondary-blue: rgb(113, 164, 252);
  --primary-red: #D72638;

  --font-primary-color: rgb(231, 49, 49);
  /* 0EB1D2 */
  
  /* --primary-btn-color: rgb(134, 134, 134); */
  --btn-primary-color: transparent;
  --btn-secondary-color: #1d0ef4;
  --btn-primary-border: solid 3px rgb(250, 244, 244);
  --btn-secondary-border: solid 3px white;
  --btn-primary-font-color: white;

  --sidebar-width: 200px;
  --mobile-navbar-height: 80px;
  --mobile-sidebar-height: 70px;
  --mobile-sidebar-padding: 10px; 

  --products-page-padding: 20px;
}


.html
{
  width: 100vw;
  background-color: lavender;
}


::-webkit-scrollbar
{
  width: 5px;
  height: 5px;
}


::-webkit-scrollbar-track
{
  background: radial-gradient(circle, rgba(251, 248, 248, 1) 58%, rgba(152, 217, 251, 1) 95%);
}


::-webkit-scrollbar-thumb
{
  height: 25px;
  background-color: dodgerblue;
  border-radius: 2em;
}


body 
{
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  box-sizing: border-box;
  background-color: red;

  overflow-x: hidden;

  
    
  
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


#root 
{
  width: 100vw;
  height: auto;
}


ul
{
  list-style-type: none;
}


.App
{
  width: 100vw;
  height: 100vh;
  position: relative;
  display: grid;
  justify-content: stretch;
  grid-template-rows: auto;
  grid-template-columns: auto repeat(4, minmax(50px, 1fr));
  grid-template-areas: "navbar navbar navbar navbar navbar"
    "sidebar log log log log"
    "sidebar log log log log"
    "sidebar log log log log"
    "sidebar log log log log";
}


.bold-span
{
  font-weight: bold;
}


.light-grey-span
{
  color: grey;
}


.navbar 
{
  height: 100px;
  width: 100%;

  position: fixed;
  z-index: 100;


  /* background-color: #daf1fd; */
  background-color: var(--secondary-blue);
  grid-area: navbar;
  display: flex;  
}


.navbar-logo-div
{
  width: 200px;
  height: 100%;
  padding: 3px;
  box-sizing: border-box;

  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  column-gap: 2px;
}


.navbar-logo-div p
{
  width: 100%;
  height: auto;

  font-size: clamp(0.85rem, 5vw, 1.5rem);
  font-weight: bold;
  font-family: 'Unbounded', cursive;
  color: white;
}


.navbar-center
{
  width: 100%;
  padding: 10px;
  display: flex;
}


.navbar-center ul 
{
  list-style-type: none;
}


.navbar-links 
{
  flex-basis: calc(100vw - 200px);

  display: flex;
  justify-content: space-evenly;
  align-items: center;
}


.navbar-btn 
{
  width: auto;
  background-color: transparent;
  border: var(--btn-primary-border);
  border-radius: 0.75rem;
  padding-block: 5px;
  padding-inline: 2px;

  font-size: clamp(0.70rem, 2vw, 1rem);
  font-weight: normal;
  font-family: 'Unbounded', cursive;
  text-align: center;
  background-color: var(--btn-primary-color);
  color: var(--btn-primary-font-color);
}


.navbar-btn:hover
{
  background-color: var(--btn-secondary-color);
  color: white;
  border: var(--btn-secondary-border);
}


.navbar-toggle-btn
{
  justify-self: end;
  height: auto;
  width: auto;
  margin-right: 10px;
  background-color: var(--secondary-blue);

  border: none;
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
  /* border: solid  2px var(--primary-blue); */

  display: none;
}


.navbar-dropdown-links
{
  width: 100% ;
  padding-block: 10px;
  position: absolute;
  z-index: 10000;
  left: 0px;
  top: var(--mobile-navbar-height);

  background-color: var(--secondary-blue);
  display: none;
}


.display-links
{
  display: flex;
  flex-direction: column;
  justify-content: start;
  row-gap: 15px;
}


.display-dropdown-links
{
  display: none;
}


.find-sheet-modal 
{
  width: 100%;
  height: 100%;
  margin: 0px;
  box-sizing: border-box;
  
  position: fixed;
  z-index: 1000;
  top: 0px;
  left: 0px;

  background-color: rgba(0, 0, 0, 0.65);

  display: none;
}


.display-find-sheet-modal
{
  display: flex;
  justify-content: center;
  align-items: center;
}


.find-sheet-modal-center 
{
  position: relative;
  width: clamp(205px, 65vw, 95%);
  height: clamp(405px, 5vh, 50%);
  

  background-color: rgba(30, 144, 255, 0.85);
  border: solid white 2px;
  border-radius: 0.75rem;
  color: white;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}


.find-sheet-list li
{
  width: 100%;
}


.find-sheet-modal-container
{
  height: 100%;
  width: 100%;

  overflow-y: auto;
  overflow-x: hidden;
  white-space: nowrap;

  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 15px;
}


.find-sheet-header
{
  font-family: 'Unbounded', cursive;
  font-weight: bold;
  font-size: clamp(1.25rem, 4vw, 2.5rem);
}


.find-sheets-list
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 10px;
}


.find-sheet-option
{
  border-radius: 0.50rem;
  padding: 5px;
  width: 100%;
  
  background-color: white;
  color: black;
  font-weight: bold;
  font-size: clamp(0.65rem, 3vw, 1.25rem);

  display: flex;
  align-items: end;
  column-gap: 10px;
}


.find-sheet-btn
{
  font-size: clamp(0.65rem, 3vw, 1.25rem);
  padding: 2px;
  border: none;
  color: white;
  background-color: red;
  border-radius: 0.25rem;

  align-self: end !important;
  justify-self: end !important;
}


.find-sheet-btn:hover
{
  background-color: rgb(255, 179, 71);;
}


.find-sheet-option span
{
  color: dodgerblue;
}


.sidebar
{
  position: fixed;
  top: 100px;
  left: 0px;
  background: linear-gradient(180deg, var(--secondary-blue) 0%, var(--primary-blue) 100%);
  width: var(--sidebar-width);
  height: 100%;

  grid-area: sidebar;
}


/*************** Workout Log Section ***************/
.workout-log 
{
  grid-area: log;
  align-self: start;

  transform: translate(var(--sidebar-width), calc(var(--mobile-navbar-height) + 2*var(--mobile-sidebar-padding)));

  width: calc(100% - var(--sidebar-width));
  height: auto;
  padding-bottom: 20px;
  background: #dfebf8;
  box-shadow: inset 5px 5px 15px rgba(0, 0, 0, 0.5);

  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}


.toolbar 
{
  width: 95%;
  height: 100px;
  background-color: transparent;

  display: grid;
  grid-template-columns: 25% 50% 25%;
  grid-template-areas: "edit-notes-btn toolbar-center new-workout-btn";
  grid-template-rows: auto; 
}


.toolbar-center 
{
  width: auto;
  font-size: clamp(0.85rem, 4vw, 1.5rem);
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  justify-self: center;
  grid-area: toolbar-center; 

  color: var(--font-primary-color);
}


.date-container
{
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  font-size: clamp(0.85rem, 4vw, 1.5rem);
  font-weight: bold;
  /* color: var(--font-primary-color); */
}


.time-stamp-span
{
  /* color: dodgerblue; */
  font-size: 0.8rem;
}


.date-btn 
{
  width: auto;
  height: 25px;

  color: inherit;
  font-size: clamp(0.85rem, 3vw, 1.5rem);  
  background-color: transparent;
  border: none;
  border-radius: 0.25rem;

  display: flex;
  justify-content: center;
  align-items: center;
}


.date-btn:hover
{
  color: var(--primary-blue);
  background-color: transparent;
}


.workout-sheet 
{
  width: 100%;
  min-height: 100vh;
  height: 100%;
  box-sizing: border-box;
  background-color: transparent;

  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
}


.new-exercise-modal
{
  position: fixed;
  z-index: 10000;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.5);
  
  display: none;
}


.new-set-modal 
{
  position: fixed;
  z-index: 10;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  overflow: hidden;

  background-color: rgba(0, 0, 0, 0.5);

  display: none;
}


.set-modal-center
{
  width: clamp(150px, 70%, 500px);
  height: clamp(200px, 50%, 250px);
  padding: 10px;

  background-color: rgb(30, 144, 255, 0.85);
  border: solid white 2px;
  border-radius: 0.75rem;

  position: relative;
  top: 50px;

  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}


.display-modal
{
  display: flex;
  justify-content: center;
}


.modal-center
{
  width: clamp(150px, 80%, 600px);
  height: clamp(200px, 50%, 350px);
  padding-block: 45px;
  padding-inline: 15px;
  

  background-color: rgb(30, 144, 255, 0.85);
  border: solid white 2px;
  border-radius: 0.75rem;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;

  position: relative;
  top: 50px;
}


.modal-form
{
  height: 60%;
  width: 100%;
  justify-self: center;
  align-self: center;

  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: auto minmax(auto, 50%);
  grid-template-rows: repeat(5, minmax(50px, auto));
  column-gap: 5px;
}


.weight-set-modal-form
{
  height: 60%;
  width: 100%;
  justify-self: center;
  align-self: center;

  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: auto minmax(auto, 50%);
  grid-template-rows: repeat(3, minmax(50px, auto));
  column-gap: 5px;
}


.cardio-input-container
{
  display: grid;
  justify-content: center;
  column-gap: 15px;
  grid-template-columns: repeat(3, minmax(25px, 25%));
}

.cardio-distance-container
{
  display: grid;
  justify-content: center;
  column-gap: 15px;
  grid-template-columns: repeat(2, minmax(75px, 45%));
}


.unit-dropdown
{
  width: 100%;
  height: 100%;

  display: grid;
  align-items: center;  
  grid-template-columns: 1fr auto;
  font-weight: bold;
}


.unit-type-container
{
  width: auto;
  position: absolute;
  top:25px;
  z-index: 100;
}


.cardio-input-container input
{
  font-size: clamp(0.75rem, 2vw, 1rem);
}


.modal-header
{
  width: 100%;
  color: var(--modal-text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 10px;
  text-transform: capitalize;
}


.close-modal-btn
{
  position: absolute;
  top: -10px;
  right: -10px;

  width: 35px;
  height: 35px;
  border: solid white 2px;
  border-radius: 50%;

  background-color: red;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
  transition: transform 0.45s linear, background-color 0.45s linear;
  
  display: flex;
  justify-content: center;
  align-items: center;
}


.close-modal-btn:hover
{
  transform: rotate(180deg);
  background-color: gray;
}


form label
{
  width: auto;
  background-color: transparent;
  border-radius: 0.25rem;
  font-size: clamp(0.65rem, 3vw, 1.25rem);
  font-weight: bold;

  text-align: end;
  color: var(--modal-text-color);
  justify-self: end;
}


form input
{
  width: 95%;
  height: auto;
  padding: 4px;
  justify-self: end;

  border-radius: 0.25rem;
  border: none;
  font-size: clamp(0.85rem, 3vw, 1rem);
}


.modal-submit-btn
{
  width: clamp(100px, 80%, 500px);

  background-color: var(--modal-text-color);
  border: solid black 2px;
  border-radius: 0.25rem;

  color: dodgerblue;
  font-weight: bold;
  font-size: clamp(0.85rem, 3vw, 1.25rem);

  justify-self: end;
}




.modal-submit-btn:hover
{
  background-color: darkgray;
  color: var(--modal-text-color);
}


.modal-error-span
{
  color: red;
  font-weight: bold;
  font-size: clamp(0.85rem, 3vw, 1rem);
}


.new-workout-btn
{
  width: auto;
  aspect-ratio: 1/1;
  padding: 5px;

  background-color: transparent;
  border: none;
  border-radius: 0.5rem; 
  color: green;
  font-weight: bolder;

  font-size: clamp(1.25rem, 3.5vw, 1.5rem);

  grid-area: new-workout-btn;
  align-self: center; 
  justify-self: end;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
} 


.edit-notes-btn 
{
  width: auto;
  aspect-ratio: 1/1;
  padding: 5px;

  background-color: transparent;
  border: none;
  border-radius: 0.5rem;
  color: rgb(255, 106, 0);
  font-weight: bolder;

  font-size: clamp(1.25rem, 3.5vw, 1.5rem);

  grid-area: edit-notes-btn;
  align-self: center;
  justify-self: start;
  margin-right: 25px;


  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.edit-notes-btn:hover
{
  color: white;
  background-color: rgb(255, 106, 0);
}


.notes-span
{
  font-size: clamp(0.5rem, 2vw, 0.75rem);
}


.notes-modal
{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 100;

  display: flex;
  justify-content: center;

  background-color: rgba(0, 0, 0, 0.5);
  transform: scale(0);
  transition: .50s linear;
}


.display-notes-modal
{
  transform: scale(1);
}


.new-workout-btn:hover
{
  background-color: green;
  color: white;
}

.notes-modal-center
{
  width: clamp(200px, 95%, 800px);
  padding: 10px;
  background-color: white;
  border-radius: 1rem;

  position: absolute;
  top: 50px;
}


.notes-form
{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}


.notes-text-area
{
  font-size: clamp(1rem, 5vw, 1.5rem);
  width: 90%;
  aspect-ratio: 3/2;
  padding: 5px;
}


.notes-btn-container
{
  width: 90%;

  display: flex;
  padding-inline: 5px;
  padding-block: 5px;
  justify-content: space-between;
}


.notes-btn
{

  
  border: none;
  border-radius: 0.5rem;
  padding: 5px;
  background-color: cornflowerblue;

  font-size: clamp(0.75rem, 3vw, 1.25rem);
  font-weight: bold;
  color: white;
}


.save-notes-btn
{
  animation: save-notes-animt 2s infinite linear;
  background-color: red;
}


@keyframes save-notes-animt
{
  0% {scale: 1;}
  50% {scale: 1.20}
  100% {scale: 1}
}


.new-workout-btn span
{
  font-size: clamp(0.5rem, 2vw, 0.75rem);
}


.workout-set
{
  width: 95%;
  height: auto;
  padding: 10px;

  background-color: rgb(149, 194, 245)   ;
  border-radius: 0.5rem;
  box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  row-gap: 10px;
  column-gap: 5px;
}


.workout-set:hover
{
  box-shadow: 9px 9px 9px rgba(0, 0, 0, 0.65);
}


.workout-set:nth-of-type(even)
{
  background-color: #83ca8d;
}


.set-center
{
  display: flex;
  column-gap: 10px;

  overflow: auto;
  white-space: nowrap;
}


.set-center::-webkit-scrollbar 
{
  scroll-margin-top: 15px;
  height: 10px;

  border: 1px solid black;
  border-radius: 0.25rem;
  background-color: lightblue;
}


.set-info
{
  position: relative;
  z-index: 1;
  width: auto;

  text-transform: capitalize;
  color: dodgerblue;

  display: grid;
  grid-template-columns: 33% 33% 33%;
  align-items: center;
}


.delete-exercise-btn
{
  width: auto;
  aspect-ratio: 1/1;

  background-color: transparent;
  border: none;
  border-radius: 0.25rem;
  color: red;
  font-weight: bold;

  font-size: clamp(1rem, 3vw, 1.25rem);
  justify-self: start;
  display: flex;
  justify-content: center;
  align-items: center;
}


.delete-exercise-icon:hover
{
  color: black;
}


.set-title
{
  font-size: clamp(0.85rem, 2vw, 1.1rem);
  font-weight: bold;
  justify-self: center;
  grid-area: 1/2/2/3;

  color: rgb(71, 70, 70);
}



.edit-title-form
{
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 15px;
}


.edit-title-btn
{
  padding-block: 3px;
  padding-inline: 15px;
  background-color: dodgerblue;
  color: white;
  font-weight: bold;
  font-size: clamp(1rem, 3vw, 1.05rem);
  border: solid white 2px;
  border-radius: 0.75rem;
}


.edit-title-btn:hover
{
  background-color: orange;
}


.set-btn-container
{
  width: auto;
  display: flex;
  justify-content: end;
  column-gap: 15px;
}


.edit-exercise-btn
{
  
  background-color: transparent;
  border: solid transparent 2px;
  
}


.edit-exercise-icon
{
  padding: 3px;
  color: purple;
  height: auto;
  aspect-ratio: 1/1;
  font-size:2rem;
}


.edit-exercise-icon:hover
{
  color: red;
}


.new-set-btn 
{
  width: auto;
  aspect-ratio: 1/1;

  background-color: transparent;
  border: none;
  border-radius: 50%;

  color: green;
  font-weight: bold;
  font-size: clamp(1rem, 3vw, 1.5rem);

  justify-self: flex-end;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  grid-area: 1/3/2/4;
}




.plus-circle-icon:hover
{
  width: 100%;
  aspect-ratio: 1/1;
  color: white  ;
  border: transparent;
  border-radius: 50%;
}


.selector-div
{
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}


.select-type-btn
{
  width: clamp(65px, 90%, 400px);
  height: auto;
  padding: 5px;
  box-sizing: border-box;
  border: none;

  background-color: white;
  border-radius: 0.25rem;
  font-weight: bold;
  font-size: 0.95rem;
  text-transform: capitalize;
  

  display: grid;
  grid-template-columns: 1fr auto;
  align-items: center;
}


.set-btn:hover
{
  background-color: orange;
}


.workout-type
{
  width: clamp(65px, 90%, 400px);
  height: auto;
  padding-top: 3px;
  background-color: transparent;
  border-radius: 0.25rem;

  display: flex;
  flex-direction: column;
  row-gap: 5px;
}


.workout-type-container
{
  display: flex;
  flex-direction: column;
}


.type-btn
{
  width: 100%;
  background-color: rgb(70, 130, 180);
  border: solid white 2px;
  border-radius: 0.25rem;
  padding: 5px;

  text-transform: capitalize;
  font-weight: bold;
  color: white;
}


.weights-set
{
  background-color: #E23D28;
  color: white;
}


.cardio-set
{
  background-color: blue;
  color: white;
}


.type-btn:hover
{
  background-color: rgb(210, 210, 210);
  color: dodgerblue;
}


.set-container
{
  display: flex;
  justify-content: flex-start;
  column-gap: 10px;
}


.single-set
{
  width: clamp(auto, 10vw, 150px);
  height: auto;
  padding: 4px;
  background-color: white;
  border: solid darkgray 2px;
  border-radius: 0.25rem;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 3px;

  font-size: clamp(0.80rem, 3vw, 1.0rem);
}


.delete-set-btn
{
  width: auto;
  height: auto;
  padding: 3px;

  border: none;
  border-radius: 0.25rem;
  background-color: transparent;
  color: red;

  align-self: center;
  display: flex;
  align-items: space-between;
}


.set-num
{
  align-self: center;
  width: 100%;
  background-color: dodgerblue;
  border-radius: 0.45rem;
  color: white;
  vertical-align: middle;
}


.interval-num 
{
  align-self: center;
  width: 100%;
  background-color: red;
  border-radius: 0.45rem;
  color: white;
  vertical-align: middle;
}


.delete-set-btn:hover
{
  color: white;
  background: red;
}



code 
{
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


/************************************************/
/******************* Sidebar ********************/
/************************************************/
.sidebar-center
{
  display: flex;
  justify-content: center;
}


.sidebar-list
{
  width: 100%;
  margin-top: 20px;

  background-color: transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 25px;
}

.sidebar-btn
{
  width: 165px;
  background-color: var(--btn-primary-color);
  padding: 2px;
  border: var(--btn-primary-border);
  border-radius: 0.75rem;

  font-size: clamp(0.85rem, 3vw, 1rem);
  font-family: 'Unbounded', cursive;
  font-weight:normal;
  color: var(--btn-primary-font-color);

  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 7px;
}


.sidebar-btn:hover
{
  background-color: var(--btn-secondary-color);
  color: white;
}


/************************************************/
/******************* Products *******************/
/************************************************/
.products-page
{
  grid-area: log;
  
  width: calc(100% - var(--sidebar-width));
  height: calc(100% + var(--mobile-navbar-height) + var(--mobile-sidebar-height));
  transform: translate(var(--sidebar-width), calc(var(--mobile-navbar-height) + var(--products-page-padding)));
  background-color: #daf1fd;
}


.products-header
{
  color: red;
  font-family: 'Unbounded', cursive;
}

.products-page-center
{
  background-color: #daf1fd;
  width: 100%;
  height: 100%;
  padding-top: var(--products-page-padding);

  display: grid;
  justify-content: center;
  justify-items: center;
  align-items: center;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  row-gap: 40px;
}


.single-product
{
  width: clamp(150px, 80%, 500px);
  padding: 10px;
  border: solid white 2px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 0.75rem;
  box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.5);

  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  row-gap: 10px;
}


.single-product-center
{
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  row-gap: 10px;
}


.info-div
{
  width: 95%;
  height: auto;
  padding: 5px;
  border-radius: 0.5rem;

  background-color: rgb(230, 230, 230);

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  row-gap: 10px;
}


.single-product span
{
  text-align: start;
  width: 100%;
  font-size: clamp(0.75rem, 2vw, 1rem);
  word-wrap: break-word;
}


.single-product-img
{
  width: clamp(100px, 45%, 250px);
  aspect-ratio: 3/4;
}


.product-name
{
  width: 100%;
  height: 75px;

  font-weight: bold;
  font-size: clamp(0.5rem, 25vw, 1rem) !important;
  text-align: center !important;
  color: red;
  border-bottom: solid dodgerblue 4px;

  display: flex;
  justify-content: center;
  align-items: center;
}


.product-desc
{
  color: dodgerblue;
  font-weight: bold;
}


.product-btn
{
  align-self: center;
  width: clamp(100px, 15vw, 150px);
  padding: 10px;
  border-radius: 0.5rem;

  background: linear-gradient(0deg, rgba(223, 162, 6, 1) 11%, rgba(248, 237, 109, 1) 100%);
  font-size: clamp(0.9rem, 3vw, 1.25rem);
  font-weight: bold;
}


.product-btn:hover
{
  background: linear-gradient(180deg, rgba(223, 162, 6, 1) 11%, rgba(248, 237, 109, 1) 100%);
}


/*****************************************************************************/
/******************************** Weight Log *********************************/
/*****************************************************************************/
.weight-log
{
  display: none;
}


.show-weight-log
{
  padding-block: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  
  position: absolute;
  z-index: 100000;

  transform: translate(var(--sidebar-width), calc(var(--mobile-navbar-height) + 2*var(--mobile-sidebar-padding)));
  width: calc(100% - var(--sidebar-width));
  height: 100vh;

  background-color: lightblue; 
}


.weight-log-title
{
  font-family: 'Unbounded', cursive;
  color: #0a25ee;
}


.close-weight-log-btn
{
  position: absolute;
  top: 10px;
  right: 10px;

  width: 35px;
  height: 35px;
  border: solid white 2px;
  border-radius: 50%;

  background-color: #ff0000;
  
  color: white;
  font-weight: bold;
  font-size: 1.5rem;
  transition: transform 0.45s linear, background-color 0.45s linear;

  display: flex;
  justify-content: center;
  align-items: center;
}


.close-weight-log-btn-shadow
{
  position: absolute;
  z-index: -1;
  top: 10px;
  right: 10px;

  width: 35px;
  height: 35px;
  border-radius: 50%;
  border: none;

  background-color: transparent;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}




.close-weight-log-btn:hover 
{
  transform: rotate(180deg);
  background-color: rgb(145, 144, 144);
}


.weight-log-btns
{
  /* background-color: red; */
  width: 100%;
  display: flex;
  justify-content: space-evenly;
}


.weight-log-btn
{
  /* width: clamp(100px, 3vw, 125px); */
  width: auto;
  height: auto;
  padding-inline: 10px;

  font-size: clamp(0.75rem, 3vw, 1.1rem);
  font-family: 'Unbounded', cursive;
  border-radius: 0.25rem;
  padding-block: 3px;

  background-color: var(--primary-blue);
  color: #ffffff;
  border: solid 2px #ffffff;

  display: flex;
  column-gap: 5px;
  justify-content: space-evenly;
  align-items: center;
}


.weight-log-btn:hover 
{
  background-color: gray;
  color: #ffffff;
  box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 0.5);
}


.weight-log-center
{
  width: 95%;
  padding-block: 15px;
  border-radius: 0.75rem;
  box-shadow: inset 5px 5px 5px rgba(0, 0, 0, 0.5);


  display: flex;
  align-items: center;
  flex-direction: column;
  row-gap: 15px;

  background-color: rgb(52, 148, 244);
}

.weight-entry
{
  width: 90%;
  padding-block: 8px;
  background-color: rgba(245, 168, 12, 0.975);
  border-radius: 1rem;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.3);

  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}


.weight-entry:hover
{
  box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.65);
}


.weight-entry-date
{
  color: #ffffff;
  font-size: clamp(1rem, 3vw, 1.2rem);
  font-family: 'Unbounded', cursive;
}


.weight-info
{
  width: 95%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}


.weight-info p
{
  color: inherit;
  font-size: 1.15rem;
  font-weight: bold;
}


.weight-span
{
  color: inherit;
  font-family: 'Unbounded', cursive;
  font-weight: bold;
  font-size: clamp(1rem, 3vw, 1rem);
}


.delete-weight-btn
{
  color: white; 

  width: clamp(100px, 5vw, 110px)!important ;
  padding-inline: 5px;
  padding-block: 3px;

  border: solid 2px #ffffff;
  border-radius: 0.25rem;

  background-color: #ff0000;
  font-size: clamp(0.9rem, 3vw, 1.2rem);
  font-weight: bold;
  
}


.delete-weight-btn:hover
{
  background-color: firebrick;
  box-shadow: inset 3px 3px 3px rgba(0, 0, 0, 0.5);
}


.weight-log-modal
{
  display: none;
}


.show-weight-log-modal
{
  display: flex;
  justify-content: center;
  align-items: start;

  width: 100%;
  height: 100%;
  padding-top: 40px;

  position: absolute;
  top: 0px;
  left: 0px;

  background-color: rgba(0, 0, 0, 0.75);
}


.weight-log-modal-center
{
  width: 90%;
  height: auto;
  position: relative;
  border-radius: .75rem;
  padding: 20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 15px;

  background-color: var(--secondary-blue);
}


.weight-log-title
{
  font-family: 'Unbounded', cursive;
  color: #ffffff;
}


.weight-log-input
{
  width: 100%;
}


.weight-log-submit-btn
{
  width: 100%;
}


.close-weight-modal-btn
{
  width: 35px;
  height: 35px;
  background-color: #ff0000;

  border-radius: 50%;
  border: solid #ffffff 2px;

  background-color: red;
  color: white;
  font-weight: bold;
  font-size: 1.5rem;

  position: absolute;
  top: -5px;
  right: -5px;
  
  transition: transform 0.45s linear, background-color 0.45s linear;
}


.close-weight-modal-btn:hover 
{
  transform: rotate(180deg);
  background-color: rgb(145, 144, 144);
}


/*****************************************************************************/
/****************************** Product Filters ******************************/
/*****************************************************************************/
.filters-center
{
  width: 100%;
  padding: 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


.product-type-span
{
  font-weight: bold;
}


.product-type-div
{
  width: 100%;
  height: 70px;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.product-filter-form
{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
}


.product-form-btn
{
  background-color: blue;
  color: white;
  font-size: clamp(0.85rem, 4vw, 1.5rem);
}


.product-form-btn:hover
{
  background-color: plum;
  border: solid dodgerblue 2px;
}


/*****************************************************************************/
/****************************** Contact Section ******************************/
/*****************************************************************************/
.contact-section
{ 
  width: 100%;
  height: 100%;
  position: absolute;
  top: calc(var(--mobile-navbar-height) + var(--products-page-padding));
  background-color: #daf1fd;
  flex-wrap: wrap;

  display: flex;
  flex-direction: column;
  align-content: center;
}


.contact-form
{
  width: 80%;
  height: 400px;


  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  row-gap: 10px;
  padding-block: 10px;

  border: solid dodgerblue 3px;
  border-radius: 0.75rem;
  background-color: red;
  box-shadow: 15px 15px 15px rgba(0, 0, 0, 0.3);

  font-size: clamp(0.95rem, 3vw, 1.25rem);
}


.contact-form input
{
  font-size: inherit;
}


.contact-text-area
{
  width: 95%;
  height: clamp(200px, 5vw, 500px);

  font-size: inherit;
}

.contact-section h1
{
  color: dodgerblue;
}

.contact-form-btn-div
{
  width: 95%;
  display: flex;
  justify-content: end;
}

.contact-form-btn
{
  padding: 3px;
  width: clamp(80px, 15vw, 120px);

  background-color: dodgerblue;
  
  border: solid white 2px;
  border-radius: 0.25rem;

  color: white;
  font-size: clamp(1rem, 3vw, 1.1rem);
  font-weight: bold;
}


.contact-form-btn:hover
{
  background-color: gray;
  /* color: dodgerblue; */
}








@media screen and (max-width: 600px)
{
  .body
  {
    width: 100%;
    height: 100vh;
  }


  .App 
  {
    width: 100vw;
    height: 100vh;
    
    display: grid;
    justify-content: center;
    grid-template-rows: auto;
    grid-template-columns: auto repeat(4, minmax(50px, 1fr)); 
    grid-template-areas: "navbar navbar navbar navbar navbar"
      "sidebar sidebar sidebar sidebar sidebar"
      "log log log log log"
      "log log log log log"
      "log log log log log"
      ;
  }


  .navbar
  {
    width: 100vw;
    height: var(--mobile-navbar-height);
    border-bottom: solid dodgerblue 2px;

    position: fixed;
    z-index: 100;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: space-between;
  }


  .navbar-logo-div 
  {
    width: auto;
    height: var(--mobile-navbar-height);
    padding: 3px;
  }


  .navbar-center
  {
    justify-content: space-between;
    height: 75px;
    align-items: center;
  }


  .navbar ul
  {
    list-style-type: none;
  }


  .navbar-links
  {
    display: none;
  }  
  

  .navbar-btn
  {
    width: clamp(150px, 5vw, 170px);
    font-size: clamp(0.9rem, 3vw, 1rem);
  }
  
  .navbar-toggle-btn
  {
    display: inline-block;
    color: white;
    height: 50px;
  }


  .navbar-logo-div
  {
    border: none;
  }


  .display-dropdown-links 
  {
    display: flex;
    flex-direction: column;
    justify-content: start;
    row-gap: 15px;
  }


  .workout-log
  {
    left: 0px;
    width: 100%;
    transform: translateY(var(--mobile-navbar-height));
  }


  .workout-sheet
  {
    box-sizing: border-box;
    height: auto;
    margin-bottom: calc(var(--mobile-sidebar-height) );  
  }


  .workout-log
  {
    position: relative;
    top: calc(--mobile-navbar-height);

    width: 100vw;
    height: auto;
  }


  .sidebar 
  {
    width: 100%;
    height: var(--mobile-sidebar-height);
    background: var(--primary-blue);

    position: relative;
    top: var(--mobile-navbar-height);
    z-index: 10;


    display: flex;
    justify-content: center;
    align-items: center;
  }


  .sidebar-list
  {
    margin: 0px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    column-gap: 5px;
  }

  .sidebar-center
  {
    width: 100%;
  }

  .sidebar-btn
  {
    font-size: 0.7rem;

    display: flex;
    flex-direction: column;
    width: 75px;
    height: 50px;
  }


  /* Products Section */
  .products-page
  {
    width: 100%;
    grid-area: log;
    transform: translate(0px, var(--mobile-navbar-height));
  }


  .display-modal 
  {
    padding-top: 50px;
    display: flex;
    justify-content: center;
    align-items: start;
  }
  /*****************************************************************************/
  /****************************** Product Filters ******************************/
  /*****************************************************************************/
  .show-weight-log 
  {
    width: 100%;
    transform: translate(0px, var(--mobile-navbar-height));
  }



  /*****************************************************************************/
  /****************************** Product Filters ******************************/
  /*****************************************************************************/
  .products-filters 
  {
    height: 100px;
    width: 90%;
    
    display: flex;
    justify-content: center;
    align-items: center;
  }


  .product-filter-form 
  {
    row-gap: 5px;
  }


  .contact-section 
  {
    width: 100%;
    height: 100%;
    position: absolute;
    top: calc(var(--mobile-navbar-height) );
  }
}